.card-container {
  width: 105%;
  padding: 1rem;
  border: 1px solid #ccc;
  box-shadow: 0px 2px 8px 1px #ccc;
  background-color: var(--white1);
  margin-bottom: 1rem;
  cursor: pointer;
}

.card-id-wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.card-id {
  color: var(--textgrey);
  font-size: .95rem;
  font-weight: 500;
}

.card-profile-photo {
  position: relative;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  overflow: hidden;
}


.card-profile-initial {
  text-transform: uppercase;
  background-color: #b96b28;
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}





.card-profile {
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-profile img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}


.card-profile-initial-available {
  height: 10px;
  width: 10px;
  background-color: var(--grey1);
  border: 1.5px solid var(--white1);
  position: absolute;
  z-index: 0;
  border-radius: 50%;
  right: 0;
  bottom: 0;
  position: relative;
  top: 10px;
  left: -8px;
}

.card-profile-initial-available-true {
  background-color: rgb(72, 223, 12);
}



.card-title {
  font-size: 1rem;
  font-weight: 600;
  color: var(--black1);
}

.card-tag {
  display: flex;
  align-items: center;
  margin-top: .8rem;
}

.card-tag-icon {
  border: 1px solid var(--grey1);
  border-radius: .3rem;
  width: fit-content;
  height: 25px;
  display: flex;
  align-items: center;
  margin-right: .5rem;
}

.card-tag-icon>svg {
  color: var(--textgrey);
  height: 20px;
}

.card-tag-box {
  border: 1px solid var(--grey1);
  padding: .3rem;
  border-radius: .3rem;
  width: fit-content;
}

.card-tag-title {
  font-size: .85rem;
  font-weight: 500;
  color: var(--textgrey);
}

.card-tag-title::before {
  display: inline-block;
  content: " ";
  height: 10px;
  width: 10px;
  background-color: var(--textgrey);
  border-radius: 50%;
  margin-right: .2rem;
}